.mySelectionsProgrammes {
  .accordion__link {
    margin-bottom: rem-calc(20*2/3);
  }

  &__actions {
    @extend .ul-unstyled;
    @include display(flex);
    @include flex-direction(column);
    position: absolute;
    right: ($column-gutter-small / 2);
    z-index: 1;

    .display-status {
      @include position(relative, null 0 null null);
      margin-bottom: rem-calc(20*2/3);
    }
  }

  .opened {
    .mySelectionsProgrammes__actions {
      @include flex-direction(column);
      @include align-items(flex-end);

      [data-app-accordion-link] {
        visibility: hidden;
        position: absolute;
      }
    }

    .mySelectionsProgramme__small {
      opacity: 0;
    }
    .mySelectionsProgramme__large {
      opacity: 1;
    }
  }

  > :last-child {
    margin-bottom: 0;
  }
}

.mySelectionsProgramme {
  .prices {
    @extend .ul-unstyled;

    li {
      @include display(flex);
      @include justify-content(space-between);
      @include align-items(center);
      padding: rem-calc(15*2/3) 0;
      border-top: 1px solid $mediumGrey;
    }

    .text {
      color: $grey;
      margin-right: rem-calc(10*2/3);
      margin-bottom: 0;
      display: inline-block;
      vertical-align: middle;
      font-size: rem-calc(24*2/3);

      strong {
        font-size: rem-calc(35*2/3);
      }
    }
  }

  &__small {
    opacity: 1;
    @include transition-property(opacity);
    @include transition-duration(.3s);
    @include transition-timing-function(ease-out);

    .media {
      margin-bottom: rem-calc(35*2/3);

      &__img {
        width: rem-calc(150*2/3);
      }

      &__content {
        padding-right: rem-calc(55*2/3);

        .heading {
          padding-top: 0;
          margin: rem-calc(-4) 0 rem-calc(20*2/3) 0;

          &__title--sub {
            margin-bottom: rem-calc(18*2/3);
          }
        }
      }
    }

    .prices {
      li:last-child {
        padding-bottom: 0;
      }
    }
  }

  &__large {
    @include transition-property(opacity);
    @include transition-duration(.3s);
    @include transition-timing-function(ease-out);
    padding-top: rem-calc(40);
    opacity: 0;

    .heading {
      padding: rem-calc(20*2/3) 0;
      margin-top: rem-calc(25*2/3);

      .heading__title {
        font-size: rem-calc(30*2/3);
      }

      @media #{$small-only} {
        &:before {
          content: "";
          @include position(absolute, 0 null null 0);
          width: rem-calc(45*2/3);
          height: rem-calc(4);
          background: $jet;
          @include flex-basis(0);
        }
      }
    }

    .text {
      margin-bottom: rem-calc(30*2/3);
      .heading__title {
        margin-bottom: rem-calc(10*2/3);
      }
    }

    .squaredImageItem__actions {
      padding: rem-calc(35*2/3) 0 0;
      border-top: 1px solid $mediumGrey;
      @extend .ul-unstyled;
      .btn-rounded {
        margin-bottom: rem-calc(15*2/3);
        display: block;
      }

      .sharing__items {
        margin-top: rem-calc(30*2/3);
      }
    }
  }

  .contact {
    @extend .ul-unstyled;
    padding: rem-calc(30*2/3) 13%;

    &__item {
      > * {
        margin-bottom: rem-calc(15*2/3);
        display: block;
      }

      &:last-child {
        > * {
          margin-bottom: 0;
        }
      }
    }

    .text {
      text-align: center;
      color: $jet;
      font-family: $font-bold;
      font-weight: $font-weight-normal;
      margin-bottom: rem-calc(30*2/3);
      strong {
        display: block;
        color: $blue;
      }
    }
  }
}


@media #{$medium-up} {
  .mySelectionsProgramme {
    &__small {
      @include display(flex);
      @include justify-content(space-between);
      @include align-items(center);

      .media {
        &__content {
          @include display(flex);
          @include justify-content(space-between);
          @include align-items(center);
        }
      }
    }

    &__large {
      @include display(flex);
      @include justify-content(space-between);

      .squaredImageItem__actions {
        @include display(flex);
        @include justify-content(space-between);
        @include align-items(center);
      }
    }

    .prices {
      li {
        border: none;
      }
    }
  }
}


.articles {
  @media #{$medium-up} {
    @include display(flex);
    @include flex-flow(row wrap);
    @include justify-content(space-between);
  }

  article {
    @include flex(1);
    @media #{$medium-up} {
      max-width: 32%;
      min-width: 32%;
    }

    @media #{$large-up} {
      max-width: 24%;
      min-width: 24%;
    }
  }
}

.searches {
  @include display(flex);
  @include flex-flow(row wrap);
  width: 100%;

  > li {
    @include flex(1 1 100%);
    min-width: 100%;
    max-width: 100%;

    @media only screen and (min-width: 400px) {
      @include flex(1 1 50%);
      min-width: 50%;
      max-width: 50%;
    }

    @media #{$medium-up} {
      min-width: default;
      max-width: none;

      &:last-child {
        table {
          margin-bottom: 0;
        }
      }
    }
  }

  table {
    width: 100%;
    padding: rem-calc(16);
    position: relative;
  }

  tr {
    &:nth-child(odd) {
      td:first-child {
        color: $jet;
      }
    }

    &:nth-child(even) {
      td:first-child {
        color: $blue;
      }
    }
  }

  td {
    color: $grey;
    font-size: rem-calc(19);
    font-weight: bold;
    width: 100%;
    padding: rem-calc(6);

    @media #{$small-up} {
      width: auto;
    }

    @media #{$medium-up} {
      padding: rem-calc(4);
    }

    &.search__exec {
      @media #{$medium-up} {
        text-align: right;
        width: rem-calc(140px);
        padding-right: rem-calc(16px);
      }
    }

    &.search__remove {
      @media #{$small-only} {
        position: absolute;
        top: 0;
        right: 0;
        width: inherit;
      }

      @media #{$medium-up} {
        text-align: right;
        width: rem-calc(34px);
      }
    }
  }

  .btn-primary {
    margin-top: rem-calc(16);
    margin-bottom: 0;
    width: 100%;

    @media #{$medium-up} {
      margin-top: 0;
      width: auto;
    }
  }

  @media #{$small-only} {
    table, thead, tbody, th, td, tr {
      display: block;
    }
  }

  @media #{$medium-up} {
    display: block;
  }
}
